export default {
	data() {
		return {
			score:0,
			num:0,
			
		}
	},
	onLoad(options) {
		console.log(options);
		if(options.score&&options.num){
			this.score=options.score
			this.num=options.num
			
		}
	},
	methods: {
		toHome(e){
			uni.reLaunch({
				url:"../Home/Home"
			})
		},
		toPre(){
			uni.reLaunch({
				url:"../examinePre/examinePre"
			})
		},
		toAgain(){
			uni.reLaunch({
				url:"../examine/examine"
			})
		},
		toError(){
			uni.redirectTo({
				url:"../examError/examError"
			})
		}
	}
}
